.container_body {
    width: 70%;
    height: 65%;
    background-color: #21838a;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;


}

.title_rate,
.star_rate {
    font-size: 1.4rem;
    color: #fff;
    width: 100%;
    padding: 2px;
    box-sizing: border-box;
}

.star_rate {
    position: relative;
}

.image_manager {
    width: 33%;
    height: 45%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: #fff;
}

.manager_rate {
    font-size: 1.2rem;
    color: #fff;
    width: 100%;
    padding: 2px;
    box-sizing: border-box;
}

.row {
    margin: 5px 0;
}

.icon_star {
    position: absolute;
    left: 80px;
    top: -10px;
    width: 50px;
    height: 50px;
    color: orange;
}