.user_card {
    width: 100%;
    height: 24%;
    border-radius: 5px;
    padding: 3px;
    box-sizing: border-box;
    background-color: #21838a;
    margin: 5px 0;
}

.body_content {
    margin-top: 8px;
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

.title_user {
    width: 100%;
    border-bottom: 2px solid #fff;
    padding: 1px;
    box-sizing: border-box;
    color: #fff;
    font-size: 1rem;
    font-weight: bold;

}